body {
  margin: 0 auto;
  font-family: "PTSans", "Arial", sans-serif;
  min-height: 320px;
  text-transform: uppercase;
  text-align: center;

  @media (min-width: @desktop-width) {
    width: 1200px;
    background-color: #f2f2f2;
  }
}


.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
